import React, { Component } from 'react';
import { connect } from 'react-redux';

import ReduxActions from '../../Redux/Actions';

class CustomerReport extends Component {
  constructor(props){
   super(props);
   this.goBack = this.goBack.bind(this);
   this.state = {
      email: '',
    };
  }
  goBack(){
    this.props.history.goBack();
  }
  _getCustomerInfo = () => {
    this.state.email = ''
    this.getCustomerInfo(this.state.email);
  }
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">CustomerReport</h1>
        </header>
        <label>Email</label>
        <input type="submit" value="Submit" />
        <button onClick={this._getCustomerInfo}>Go Back</button>
        <div>
          <button onClick={this.goBack}>Go Back</button>
        </div>
      </div>

    );
  }
}

const mapStateToProps = ( reports ) => {
  const { customerInfo } = reports;

  return { customerInfo };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getCustomerInfo: (email) =>
      dispatch(ReduxActions.reportsGetCustomerInfoAttempt(email)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(CustomerReport);



